.news{
	
	width: 100%;
	display: flex;
	flex-direction: column;
	gap: $size-l1;

	&__container{

		width: 100%;
		display: grid;
		grid-template-columns: 1fr;
		gap: $size-r;
		background-color: $color-b1;
		border-radius: $size-r;
		box-shadow: .5rem .1rem $size-r .1rem rgba(0, 0, 0, .25);

	}

	&__img-container{

		width: 100%;

		img{

			border-top-left-radius: $size-r;
			border-top-right-radius: $size-r;
			width: 100%;
			height: 100%;
			object-fit: cover;

		}

	}

	&__content{

		display: flex;
		flex-direction: column;
		padding: $size-r;
		gap: $size-l3;

	}

	&__text-container{

		display: flex;
		flex-direction: column;
		gap: $size-r;

		h3{

			font:{

				family: $font-tomorrow;
				size: $size-r;
				weight: 700;

			}

		}

		p{

			font:{

				size: $size-r * .8;
				weight: 400;

			}

		}

	}

	&__footer{

		width: 100%;
		display: flex;
		gap: $size-l1;
		justify-content: space-between;

	}

	&__social-buttons{

		display: flex;
		gap: $size-s2;

	}

	&__like{

		display: flex;
		justify-content: center;
		align-items: center;
		outline: none;
		border: none;
		background-color: transparent;
		cursor: pointer;

		svg{

			color: rgba(127, 127, 127, 1.0);
			font-size: $size-r * 1.25;

		}

		&--activated{

			svg{

				color: $color-f1;

			}

		}

	}

}