.main{

	display: flex;
	flex-direction: column;
	background-color: $color-b1;
	border-top-left-radius: $size-l1;
	border-top-right-radius: $size-l1;
	border-bottom-left-radius: $size-l1;
	border-bottom-right-radius: $size-l1;
	margin: 0 $size-r $size-r $size-r;
	padding: $size-r $size-r $size-l3 $size-r;
	gap: $size-r;

	&__separator{

		width: 100%;
		height: $size-r * .1;
		background-color: $color-a1_25;

	}

	.section-selector{

		position: sticky;
		margin: 0 $size-r * -1;
		top: 0;
		background-color: $color_b1_75;
		backdrop-filter: blur($size-s1);
		z-index: 100000;

		&__ul{

			width: 100%;
			display: flex;
			justify-content: center;
			gap: $size-l2;
			padding: $size-r;

		}

		&__item{

			font:{

				family: $font-space_mono;
				size: $size-s3;
				weight: 700;

			}
			letter-spacing: .075em;
			text-transform: uppercase;
			list-style: none;
			padding-bottom: .1rem;

		}

	}

	.section{

		display: flex;
		flex-direction: column;
		gap: $size-l3;

		&__title{

			font:{

				family: $font-tomorrow;
				size: $size-s3;
				weight: 700;

			}
			text-transform: uppercase;

		}

	}

}