* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
}

:root {
  font-size: 100%;
}

html, body {
  scroll-behavior: smooth;
}

body {
  background-color: rgb(17, 17, 17);
}

.pages {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  width: 100%;
}
.pages__container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  background-color: #0E0C40;
  border-radius: 2rem;
  padding: 0.5rem 1rem;
}
.pages__button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  border: none;
  outline: none;
  background-color: transparent;
}
.pages__button svg {
  color: rgb(255, 255, 255);
  font-size: 2rem;
}
.pages__number {
  color: rgb(255, 255, 255);
  font-size: 1rem;
  font-weight: 100;
}

.paragraph {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.paragraph h3 {
  font-family: "Tomorrow", sans-serif;
  font-size: 1rem;
  font-weight: 700;
}
.paragraph p {
  font-size: 0.8rem;
  font-weight: 400;
}

.bg {
  background-image: url("https://www.solarsystemscope.com/textures/download/2k_stars_milky_way.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  top: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: -1000;
}
.bg__earth {
  position: absolute;
  rotate: -23deg;
  margin: auto;
  border-radius: 50%;
  filter: brightness(1.2) contrast(110%);
  /*
  position: absolute;
  top: 50vh;
  rotate: 23deg;
  margin: auto;
  border-radius: 50%;
  filter: brightness(1.2) contrast(110%);
  */
}
.bg__earth::before {
  position: absolute;
  border-radius: 100%;
  filter: blur(20px);
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, rgb(40, 151, 215), rgba(40, 151, 215, 0.1), transparent);
}
.bg__earth--texture {
  position: absolute;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  background-image: url("https://www.solarsystemscope.com/textures/download/2k_earth_daymap.jpg");
  background-size: cover;
  animation: spin 300s linear infinite;
  filter: saturate(65%);
}
.bg__earth--clouds {
  position: absolute;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  background-image: url("https://www.solarsystemscope.com/textures/download/2k_earth_clouds.jpg");
  background-size: cover;
  animation: spin 200s linear infinite;
  mix-blend-mode: screen;
  opacity: 0.5;
}
.bg__earth--mask {
  background: linear-gradient(to right, transparent, rgba(0, 0, 0, 0.85), rgb(0, 0, 0));
  border-radius: 100%;
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: 1.25rem 1.25rem -3rem -1.25rem #000 inset;
}
@keyframes spin {
  0% {
    background-position-x: 0;
  }
  100% {
    background-position-x: -200%;
  }
}

.header {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  padding: 1rem 1.5rem 2rem 1.5rem;
  z-index: 1000;
}
.header .search {
  display: flex;
  gap: 1rem;
  align-items: center;
  border-radius: 2rem;
  outline: 0.1rem solid rgb(255, 255, 255);
  padding: 0.5rem 1.5rem;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(0.25rem);
}
.header .search__input {
  background-color: transparent;
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  font-size: 1rem;
  color: rgb(255, 255, 255);
}
.header .search__input::placeholder {
  color: rgb(255, 255, 255);
  font-style: italic;
  font-weight: 100;
}
.header .search__separator {
  width: 0.1rem;
  height: 1rem;
  background-color: rgb(255, 255, 255);
}
.header .search__icon {
  background-color: transparent;
  border: none;
  outline: none;
}
.header .search__icon svg {
  color: rgb(255, 255, 255);
  font-size: 1rem;
}
.header .logo {
  color: rgb(255, 255, 255);
  font-size: 1.5rem;
  font-family: "Tomorrow", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}
.header h2 {
  color: rgb(241, 192, 46);
  font-family: "Tomorrow", sans-serif;
  font-weight: 400;
  font-style: italic;
  font-size: 1rem;
}

.main {
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  border-bottom-left-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  margin: 0 1rem 1rem 1rem;
  padding: 1rem 1rem 3rem 1rem;
  gap: 1rem;
}
.main__separator {
  width: 100%;
  height: 0.1rem;
  background-color: rgba(17, 17, 17, 0.25);
}
.main .section-selector {
  position: sticky;
  margin: 0 -1rem;
  top: 0;
  background-color: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(0.25rem);
  z-index: 100000;
}
.main .section-selector__ul {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 1rem;
}
.main .section-selector__item {
  font-family: "Space Mono", monospace;
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 0.075em;
  text-transform: uppercase;
  list-style: none;
  padding-bottom: 0.1rem;
}
.main .section {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.main .section__title {
  font-family: "Tomorrow", sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
}

.news {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.news__container {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  background-color: rgb(255, 255, 255);
  border-radius: 1rem;
  box-shadow: 0.5rem 0.1rem 1rem 0.1rem rgba(0, 0, 0, 0.25);
}
.news__img-container {
  width: 100%;
}
.news__img-container img {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.news__content {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 3rem;
}
.news__text-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.news__text-container h3 {
  font-family: "Tomorrow", sans-serif;
  font-size: 1rem;
  font-weight: 700;
}
.news__text-container p {
  font-size: 0.8rem;
  font-weight: 400;
}
.news__footer {
  width: 100%;
  display: flex;
  gap: 1.5rem;
  justify-content: space-between;
}
.news__social-buttons {
  display: flex;
  gap: 0.5rem;
}
.news__like {
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.news__like svg {
  color: rgb(127, 127, 127);
  font-size: 1.25rem;
}
.news__like--activated svg {
  color: rgb(241, 14, 84);
}

.faq {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.faq__container {
  list-style: none;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border-radius: 1rem;
  box-shadow: 0.5rem 0.1rem 1rem 0.1rem rgba(0, 0, 0, 0.25);
}
.faq__question {
  display: flex;
  align-items: center;
  padding: 1rem;
  gap: 1rem;
  border-radius: 1rem;
  cursor: pointer;
}
.faq__question h3 {
  font-family: "Tomorrow", sans-serif;
  font-size: 1rem;
  font-weight: 700;
}
.faq__separator {
  width: 0.1rem;
  height: 2rem;
  background-color: rgba(17, 17, 17, 0.25);
}
.faq__plus {
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  border: none;
  background-color: transparent;
  transition: all, 0.5s ease-in-out;
}
.faq__plus svg {
  color: rgb(127, 127, 127);
  font-size: 1.25rem;
}
.faq__plus--activated {
  transform: rotate(-45deg);
}
.faq__plus--activated svg {
  color: rgb(241, 14, 84);
}
.faq__answer {
  background-color: rgb(204, 204, 204);
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  max-height: 0;
  overflow: hidden;
  padding: 0 1rem;
  transition: all, 0.2s ease-in-out;
}
.faq__answer--activated {
  max-height: none;
  padding: 1rem;
}
.faq__answer p {
  font-size: 0.8rem;
  font-weight: 400;
  font-style: italic;
}

.about {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.comment__text {
  font-size: 0.8rem;
  font-weight: 100;
  font-style: italic;
}
.comment__text a {
  color: rgb(241, 14, 84);
  font-weight: 700;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  background-color: rgb(255, 255, 255);
  border-radius: 1rem;
  box-shadow: 0.5rem 0.1rem 1rem 0.1rem rgba(0, 0, 0, 0.25);
  padding: 1.5rem;
}
.form__input {
  display: flex;
  gap: 1rem;
  border-radius: 2rem;
  padding: 2rem 1.5rem 1rem 1.5rem;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(0.25rem);
  color: rgb(255, 255, 255);
  white-space: nowrap;
  font-size: 1rem;
  font-weight: 400;
}
.form__input input {
  position: relative;
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  border-bottom: 0.1em solid transparent;
  padding-bottom: 0.1em;
  font-size: 1rem;
  font-weight: 100;
}
.form__input input:focus {
  border-bottom: 0.1em solid #FFF45E;
  padding-bottom: 0.1em;
}
.form__input input:focus ~ i, .form__input input:valid ~ i {
  font-size: 0.8rem;
  transform: translateY(-0.75rem);
}
.form__input i {
  position: absolute;
  top: 0;
  padding: 1.5rem 0;
  transition: all 0.25s ease-in-out;
}
.form__message {
  border-radius: 1rem;
  padding: 2rem 1.5rem 1rem 1.5rem;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(0.25rem);
  color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
}
.form__message i {
  position: absolute;
  top: 0;
  padding: 1.5rem 0;
  transition: all 0.25s ease-in-out;
}
.form__message textarea {
  background-color: transparent;
  color: rgb(255, 255, 255);
  height: 8rem;
  resize: none;
  outline: none;
  border: none;
  font-size: 1rem;
  font-weight: 100;
}
.form__message textarea:focus ~ i, .form__message textarea:valid ~ i {
  font-size: 0.8rem;
  transform: translateY(-0.75rem);
}
.form__submit {
  border: none;
  border-radius: 1.5rem;
  padding: 1rem 1.5rem;
  background-color: #0E0C40;
  color: rgb(255, 255, 255);
  font-size: 1rem;
  font-weight: 400;
}

.footer {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  background-color: #0E0C40;
  padding: 1.5rem;
}
.footer .button {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.footer .button__container {
  width: 11rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  border: none;
  outline: 0.1rem solid rgba(255, 255, 255, 0.25);
  background-color: transparent;
  border-radius: 2rem;
  padding: 0.5rem 1rem;
  text-decoration: none;
}
.footer .button__text {
  font-size: 1rem;
  color: rgb(255, 255, 255);
}
.footer .button svg {
  color: rgb(255, 255, 255);
  font-size: 2rem;
}
.footer .socials {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.footer .socials__title {
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  border-bottom: 0.1em solid rgb(241, 192, 46);
  padding-bottom: 0.1em;
  letter-spacing: 0.05em;
  font-size: 1rem;
  font-family: "Space Mono", monospace;
  font-weight: 700;
}
.footer .socials__container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer .socials__item {
  list-style: none;
}
.footer .socials__item a {
  display: flex;
  align-items: center;
  gap: 1rem;
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-size: 0.8rem;
  letter-spacing: 0.1em;
}
.footer .socials__item a svg {
  font-size: 1.5rem;
}
.footer__text {
  color: rgb(255, 255, 255);
  line-height: 1.25em;
  font-size: 0.75rem;
  font-weight: 100;
}
.footer__text a {
  text-decoration: none;
  color: rgb(241, 192, 46);
}

