.header{

	display: flex;
	flex-direction: column;
	gap: $size-l2;
	width: 100%;
	padding: $size-r $size-l1 $size-l2 $size-l1;
	z-index: $z_index-f;

	.search{
		
		display: flex;
		gap: $size-r;
		align-items: center;
		border-radius: $size-l2;
		outline: $size-r * .1 solid $color-b1;
		padding: $size-s2 $size-l1;
		background-color: rgba(0, 0, 0, .5);
		backdrop-filter: blur($size-s1);

		&__input{

			background-color: transparent;
			width: 100%;
			height: 100%;
			outline: none;
			border: none;
			font-size: $size-r;
			color: $color-b1;

			&::placeholder{

				color: $color-b1;
				font:{

					style: italic;
					weight: 100;

				}

			}

		}

		&__separator{

			width: $size-r * .1;
			height: $size-r;
			background-color: $color-b1;

		}

		&__icon{

			background-color: transparent;
			border: none;
			outline: none;

			svg{

				color: $color-b1;
				font-size: $size-r;

			}

		}

	}

	.logo{

		color: $color-b1;
		font-size: $size-l1;
		font-family: $font-tomorrow;
		text-transform: uppercase;
		letter-spacing: .1em;

	}

	h2{

		color: $color-c1;
		font:{

			family: $font-tomorrow;
			weight: 400;
			style: italic;
			size: $size-r;

		}

	}

}